<template>
    <v-timeline>
      <v-timeline-item>
      
        <template v-slot:opposite>
        </template>
        <v-card class="elevation-2">
          <v-card-title class="headline">Abhimanyu Barge :- </v-card-title>
          <v-card-text>      :          .          .           .           .            .     .        . ,           .             .     ,     .    ,  ,  ,  ,  ,  ,  ,  ,  ,  ,  ,     .</v-card-text>
        </v-card>
      </v-timeline-item>

      <v-timeline-item>
      
      <template v-slot:opposite>
      </template>
      <hr>
      <v-card class="elevation-2">
        <v-card-title class="headline">  :- </v-card-title>
        <v-card-text>         .          </v-card-text>
      </v-card>
    <hr>

      <v-card class="elevation-2">
        <v-card-title class="headline">       </v-card-title>
       <v-card-text>
        <input type="file" v-on:change="handleFileInputChange">
      </v-card-text>
      
         
     
   
      </v-card>

    </v-timeline-item>
  
    </v-timeline>
  </template>

  

<script>
  export default {
    name: 'querySummery',
    created(){
      this.$store.state.openNevigation = false
    },
    data () {
      return {
        items: [
        {
          color: 'red lighten-2',
          icon: 'mdi-star'
        },
        {
          color: 'purple darken-1',
          icon: 'mdi-book-variant'
        },
        {
          color: 'green lighten-1',
          icon: 'mdi-airballoon'
        },
        {
          color: 'indigo',
          icon: 'mdi-buffer'
        }
      ]
      }
    },
    methods: {
      handleFileInputChange(event) {
      // Access the file through event.target.files
      const file = event.target.files[0];
      
      // Do whatever you need with the file
      console.log('Selected file:', file);
    }
    }
  }
</script>


